import { MaounTheme } from './../../../../@core/models/enums/theme/MaounTheme.enum';
import {
  Component,
  OnInit,
  AfterViewInit,
} from '@angular/core';
// import SwiperCore, { Swiper, Autoplay, SwiperOptions } from "swiper/core";
import { Swiper, SwiperOptions, Autoplay } from "swiper/core";
import { ThemeService } from '../../../../@core/services/theme.service';

Swiper.use([Autoplay])

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})
export class HomeComponent implements OnInit, AfterViewInit {
  headerMaouns: string[] | ArrayBuffer[] = [
    '../../../../../assets/images/landinng-home-header/maoun.png',
    '../../../../../assets/images/landinng-home-header/maoun.png',
    '../../../../../assets/images/landinng-home-header/maoun5.png',
  ]
  headerMaoun: any = this.headerMaouns[0];

  headerBoundries: string[] | ArrayBuffer[] = [
    '../../../../../assets/images/landinng-home-header/home-header1.png',
    '../../../../../assets/images/landinng-home-header/home-header2.png',
    '../../../../../assets/images/landinng-home-header/home-header5.png',
  ]
  headerBoundry: any = this.headerBoundries[0];

  headerBgs: string[] | ArrayBuffer[] = [
    '../../../../../assets/images/landinng-home-header/bg1.png',
    '../../../../../assets/images/landinng-home-header/bg2.png',
    '../../../../../assets/images/landinng-home-header/bg5.png',
  ]
  headerBg: any = this.headerBgs[0];

  headerItems: string[] | ArrayBuffer[] = [
    '../../../../../assets/images/landinng-home-header/header-image1.png',
    '../../../../../assets/images/landinng-home-header/header-image2.png',
    '../../../../../assets/images/landinng-home-header/header-image5.png',
  ]
  headerItem: any = this.headerItems[0];

  phoneHues: string[] | ArrayBuffer[] = [
    '0',
    '50',
    '223',
  ]
  phoneHue: any = this.phoneHues[0];

  swiperConfig: SwiperOptions = {
    spaceBetween: 50,
    loop: true,
    autoplay: {
      delay: 2500,
      disableOnInteraction: false
    },
    slidesPerView: this.getNumberOfSlides(),
  }

  constructor(
    private themeService: ThemeService,
  ) { }

  ngOnInit(): void {


  }

  ngAfterViewInit(): void {

    this.getAllImages()

  }

  getAllImages() {
    this.headerItems.forEach((item, i) => {
      fetch(item)
        .then(response => response.blob())
        .then(blob => {
          let reader = new FileReader();
          let componentClass = this;
          reader.onload = function () {
            componentClass.headerItems[i] = reader.result
          };
          reader.readAsDataURL(blob);
        });
    })
    this.headerMaouns.forEach((item, i) => {
      fetch(item)
        .then(response => response.blob())
        .then(blob => {
          let reader = new FileReader();
          let componentClass = this;
          reader.onload = function () {
            componentClass.headerMaouns[i] = reader.result
          };
          reader.readAsDataURL(blob);
        });
    })
    this.headerBoundries.forEach((item, i) => {
      fetch(item)
        .then(response => response.blob())
        .then(blob => {
          let reader = new FileReader();
          let componentClass = this;
          reader.onload = function () {
            componentClass.headerBoundries[i] = reader.result
          };
          reader.readAsDataURL(blob);
        });
    })
    this.headerBgs.forEach((item, i) => {
      fetch(item)
        .then(response => response.blob())
        .then(blob => {
          let reader = new FileReader();
          let componentClass = this;
          reader.onload = function () {
            componentClass.headerBgs[i] = reader.result
          };
          reader.readAsDataURL(blob);
        });
    })
    this.changeTheme();
  }

  changeTheme() {
    this.themeService.intervalTheme().subscribe(
      succ => {
        this.themeService.changeTheme(succ);
        switch (succ) {
          case MaounTheme.RESTAURANT:
            this.animate()
            this.headerMaoun = this.headerMaouns[MaounTheme.RESTAURANT - 1];
            this.headerBoundry = this.headerBoundries[MaounTheme.RESTAURANT - 1];
            this.headerBg = this.headerBgs[MaounTheme.RESTAURANT - 1];
            this.headerItem = this.headerItems[MaounTheme.RESTAURANT - 1];
            this.phoneHue = this.phoneHues[MaounTheme.RESTAURANT - 1];
            break;
          case MaounTheme.CAFE:
            this.animate()
            this.headerMaoun = this.headerMaouns[MaounTheme.CAFE - 1];
            this.headerBoundry = this.headerBoundries[MaounTheme.CAFE - 1];
            this.headerBg = this.headerBgs[MaounTheme.CAFE - 1];
            this.headerItem = this.headerItems[MaounTheme.CAFE - 1];
            this.phoneHue = this.phoneHues[MaounTheme.CAFE - 1];
            break;
          case MaounTheme.SHOP:
            this.animate()
            this.headerMaoun = this.headerMaouns[MaounTheme.SHOP - 1];
            this.headerBoundry = this.headerBoundries[MaounTheme.SHOP - 1];
            this.headerBg = this.headerBgs[MaounTheme.SHOP - 1];
            this.headerItem = this.headerItems[MaounTheme.SHOP - 1];
            this.phoneHue = this.phoneHues[MaounTheme.SHOP - 1];
            break;

          default:
            break;
        }
      },
      err => console.error(err)
    )
  }

  set(arr: string[], a: string) {

  }

  isAnimated: boolean = false;
  animate() {
    this.isAnimated = true;
    setTimeout(() => {
      this.isAnimated = false;
    }, 1000);
  }

  // onSwiper(e: Swiper) {
  //   console.log(e);
  // }

  getNumberOfSlides() {
    if (innerWidth < 1200 && innerWidth > 1000) {
      return 4
    } else if (innerWidth < 1000 && innerWidth > 700) {
      return 3
    } else if (innerWidth < 700 && innerWidth > 400) {
      return 2
    } else if (innerWidth < 400) {
      return
    } else {
      return 5
    }
  }
}
