import { Inject, Injectable } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { Subject } from 'rxjs';
import { MaounTheme } from '../models/enums/theme/MaounTheme.enum';
import { environment } from './../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ThemeService {
  themes = [MaounTheme.RESTAURANT, MaounTheme.CAFE, MaounTheme.SHOP];
  themesLegnth: number = this.themes.length;
  index: number = 1;

  theme$: Subject<any> = new Subject();

  constructor(
    @Inject(DOCUMENT)
    private doc: Document) { }

  intervalTheme(): Subject<any> {

    setInterval(() => {
      if (this.index < this.themesLegnth) {
        this.theme$.next(this.themes[this.index]);
        this.index++;
      } else {
        this.index = 0;
        this.theme$.next(this.themes[this.index]);
        this.index++;
      }
    }, 15000);

    return this.theme$;
  }

  changeTheme(themeValue: MaounTheme) {
    switch (themeValue) {
      case MaounTheme.RESTAURANT:
        this.doc.documentElement.style.setProperty('--primary-hue', environment.restaurantThemeValues.hue)
        this.doc.documentElement.style.setProperty('--primary-saturation', environment.restaurantThemeValues.saturation)
        this.doc.documentElement.style.setProperty('--primary-light', environment.restaurantThemeValues.light)
        break;
      case MaounTheme.CAFE:
        this.doc.documentElement.style.setProperty('--primary-hue', environment.cafeThemeValues.hue)
        this.doc.documentElement.style.setProperty('--primary-saturation', environment.cafeThemeValues.saturation)
        this.doc.documentElement.style.setProperty('--primary-light', environment.cafeThemeValues.light)
        break;
      case MaounTheme.SHOP:
        this.doc.documentElement.style.setProperty('--primary-hue', environment.shopThemeValues.hue)
        this.doc.documentElement.style.setProperty('--primary-saturation', environment.shopThemeValues.saturation)
        this.doc.documentElement.style.setProperty('--primary-light', environment.shopThemeValues.light)
        break;

      default:
        break;
    }
  }
}
