import { Component, OnInit, AfterViewInit } from '@angular/core';
import { ThemeService } from '../../../../@core/services/theme.service';
import { MaounTheme } from '../../../../@core/models/enums/theme/MaounTheme.enum';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.scss']
})
export class FooterComponent implements OnInit, AfterViewInit {
  footerBgs: string[] = [
    '../../../../../assets/images/landinng-home-header/footer1.png',
    '../../../../../assets/images/landinng-home-header/footer2.png',
    '../../../../../assets/images/landinng-home-header/footer5.png',
  ]
  footerBg: string = this.footerBgs[0]

  constructor(
    private themeService: ThemeService
  ) { }

  ngOnInit(): void {
  }

  ngAfterViewInit(): void {
    this.changeTheme();
  }

  changeTheme() {
    this.themeService.intervalTheme().subscribe(
      succ => {
        console.log(succ)
        this.themeService.changeTheme(succ);
        switch (succ) {
          case MaounTheme.RESTAURANT:
            this.footerBg = this.footerBgs[MaounTheme.RESTAURANT - 1];
            this.animate()
            break;
          case MaounTheme.CAFE:
            this.footerBg = this.footerBgs[MaounTheme.CAFE - 1];
            this.animate()
            break;
          case MaounTheme.SHOP:
            this.footerBg = this.footerBgs[MaounTheme.SHOP - 1];
            this.animate()
            break;

          default:
            break;
        }
      },
      err => console.error(err)
    )
  }

  isAnimated: boolean = false;
  animate() {
    this.isAnimated = true;
    setTimeout(() => {
      this.isAnimated = false;
    }, 1000);
  }

}
